.comments{
    margin-top: 40px;
    padding: 10px;
    border-radius: 10px;
    /* font-family: 'Darker Grotesque', sans-serif; */
    /* display: flex;
    flex-direction: column;
    align-items: center; */
    background: #3c424f;
  }
  .comments-title{
    color: white;
  }
  .comments-form{
    color: white;
  }
  .commentBox{
    margin-top: 40px;
    max-width: 700px;
    padding: 0px 0px 20px 0px;
    border-radius: 20px;
    background: #1b212d;
  }
  .publishDate{
    color: #787878;
    margin-left: 15px;
    font-weight: 100;
  }
  @keyframes hearts {
      0% { opacity: 0; }
    100% { opacity: 1; }
  }
  .comment{
    padding: 10px;
    margin: 20px 30px;
    color: #fff;
  }
  .createComment{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
  }
  #comment{
    font-family: 'Darker Grotesque', sans-serif;
    margin-top: 10px;
    border-radius: 15px;
    border: 1px solid #787878;
    padding: 5px 5px 5px 10px;
    height: 70px;
    transition: 0.2s;
    box-sizing: border-box;
    resize: none;
  }
  #comment:focus{
    outline: none;
    border: 1px solid #fc6f20;
  }
  #comment::placeholder{
    color: #bebebe;
  }
  .comment-submit{
    font-family: 'Darker Grotesque', sans-serif;
    color: #054231;
    width: 160px;
    color: #fff;
    font-size: 18px;
    padding: 10px;
    padding-top: 7px;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 15px;
    border: none;
    background: #fc6f20;
    margin-top: 20px;
    box-shadow: 0px 5px 0px 0px #fc6f207a;
  }
  .comment-submit:hover{
    box-shadow: none;
    transform: translateY(5px);
    transition: all .1s linear;
  }
  .comment-submit:focus{
    outline: none;
  }
  .comment{
    display: flex;
  }
  .commentPic{
    max-width: 45px;
    max-height: 45px;
  }
  .commentHeader{
    display: flex;
    align-items: center;
  }
  .commentBody{
    max-width: 580px;
    overflow-wrap: break-word;
  }
  