.card{
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: 10px auto;
    padding: 10px;
    border-radius: 10px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.leftcontent{
    display: flex;
}

.profdetails{
    margin: 0 20px;
}


.profimg{
    width: 50px;
    border: 1px solid black;
    border-radius: 50%;
}


.rightcontent{
    display: flex;
    align-items: center;
    margin-right: 20px;
}